.container {
  padding-top: 36rpx;
}

.pr {
  position: relative;
}

.share {
  font-size: 28rpx;
  cursor: pointer;
  color: #3ee9ff;
  position: absolute;
  right: 10rpx;
  top: 40rpx;
}

.test-course {
  color: rgb(0, 188, 212);
  font-size: 1.17em; margin: .83em 0
}

.course-tab {
  width: 100%;
  height: 68rpx;
  display: flex;
  margin: 0;
  line-height: 0;
  padding: 0;
  .active {
    background-color: rgb(232, 252, 255);
  }
}

.course-item {
  cursor: pointer;
  box-sizing: border-box;
  width: 25%;
  height: 100%;
  text-align: center;
  display: inline-block;
  background-color: rgb(255, 255, 255);
  border-bottom: 2rpx solid rgb(224, 224, 224);
  border-top: 2rpx solid rgb(224, 224, 224);
  transition: all 150ms linear;
}

.course-item-span {
  transition: color 0.2s linear;
  color: rgb(0, 188, 212);
  font-size: 32rpx;
  display: inline-block;
  line-height: 2em;
  overflow: hidden;
}

.course-content {
  position: relative;
}

.course-wrap {
  position: relative;
}

.course-mr30{
  margin-top: 60rpx;
}

.course-joined {
  display: inline;
  font-size: 28rpx;
  color: rgb(157, 157, 157);
}

.course-joined-num {
  display: inline;
  cursor: pointer;
  border-radius: 10rpx;
  padding: 6rpx 40rpx;
  color: rgb(255, 255, 255);
  background-color: rgb(255, 154, 154);
}

.course-joined-see {
  display: inline;
  margin-left: 16rpx;
  font-size: 20rpx;
  color: rgb(167, 167, 167);
}

.course-teacher {
  font-size: 28rpx;
  color: rgb(157, 157, 157);
}

.course-teacher-nickname {
  color: rgb(255, 154, 154);
}

.course-details {
  word-wrap: break-word;
  font-size: 28rpx;
  color: rgb(0, 188, 212);
  margin: 1em 0;
}

.course-edit {
  font-size: 28rpx;
  color: rgb(0, 188, 212);
  cursor: pointer;
  margin-top: -60rpx;
  margin-right: 20rpx;
  float: right;
}

.course-dcontent-wrap {
  color: rgb(0, 188, 212);
}

.course-dcontent {
  margin: 28rpx 0;
}

.new-warn {
  color: rgb(0, 188, 212);
  position: absolute;
  top: 10rpx;
  right: 10rpx;
  text {
    border-bottom: 2rpx solid rgb(255, 154, 154);
    color: rgb(0, 188, 212);
    font-size: 0.8em;
    line-height: 2em;
    display: inline-block;
    overflow: hidden;
    cursor: pointer;
  }
}

.no-warn {
  margin-bottom: 60rpx;
  margin-top: 80rpx;
  font-size: 50rpx;
  color: grey;
}

.new-input-warn {
  display: inline-block;
  box-sizing: border-box;
  padding: 20rpx;
  outline: none;
  width: 100%;
  height: 350rpx;
  resize: none;
  border-radius: 2rpx;
  border: 4rpx solid #eee;
}

.warn-button {
  margin-top: 100rpx;
  margin-bottom: 60rpx;
}

.warn-time {
  font-size: 24rpx;
  color: rgb(68, 122, 150);
}

.warn-text {
  padding: 10rpx;
  font-size: 32rpx;
  word-wrap: break-word;
  color: rgb(0, 188, 212);
  border: solid 2rpx #D8D8D8;
  border-left: solid 6rpx #2DC6F8;
  border-right: solid 4rpx #447A96;
  width: 93%;
  border-radius: 10rpx;
  background-color: #fff;
  font-family: monospace, monospace;
  overflow: auto;
  white-space: pre;
  margin: 1em 0 1em;
}

.comment-button-wrap {
  position: absolute;
  right: 10rpx;
  top: 32rpx;
  text {
    border-bottom: 2rpx solid rgb(255, 154, 154);
    color: rgb(255, 154, 154);
    font-size: 0.8em;
    line-height: 2em;
    display: inline-block;
    overflow: hidden;
    cursor: pointer;
  }
}

.comment-none {
  margin-bottom: 60rpx;
  margin-top: 120rpx;
  font-size: 50rpx;
  color: grey;
}

.new-comment {
  position: absolute;
  left: 10rpx;
  top: 32rpx;
  text {
    border-bottom: 2rpx solid rgb(255, 154, 154);
    font-size: 0.8em;
    color: rgb(0, 188, 212);
    line-height: 2em;
    display: inline-block;
    overflow: hidden;
    cursor: pointer;
  }
}

.course-list {
  cursor: pointer;
  background-color: #f8f8f8;
  padding-top: 30rpx;
  padding-left: 24rpx;
  border: 2rpx solid #e3e3e3;
  border-top-left-radius: 40rpx;
  border-top-right-radius: 40rpx;
  color: #5d5d5d;
  margin-left: 40rpx;
  width: 90%;
  height: 76rpx;
  transition: all .15s linear;
}

.course-index {
  display: inline-block;
  font-size: 28rpx;
}

.is-course-start {
  display: inline-block;
  font-size: 26rpx;
  margin-left: 24rpx;
  color: rgb(139, 139, 139);
}

.course-let-start {
  text-align: center;
  cursor: pointer;
  font-size: 24rpx;
  border-radius: 10rpx;
  height: 32rpx;
  margin-right: 16rpx;
  color: #fff;
  float: right;
  padding: 10rpx;
  width: 110rpx;
  background-color: #52b6ac;
}

.see-course {
  text-align: center;
  cursor: pointer;
  font-size: 24rpx;
  border-radius: 10rpx;
  height: 32rpx;
  margin-right: 16rpx;
  color: #fff;
  float: right;
  padding: 10rpx;
  width: 76rpx;
  background-color: #00bcd4;
}

.add-one-course {
  cursor: pointer;
  border-radius: 10px;
  margin-top: 20px;
  margin-bottom: 30px;
  font-size: 25px;
  color: rgb(0, 188, 212);
  border: 1px solid rgb(203, 208, 207);
  padding: 5px 50px;
  background-color: #e0e0e0;
}

.course-complete {
  font-size: 28rpx;
  color: rgb(255, 154, 154);
  cursor: pointer;
  margin-top: -60rpx;
  margin-right: 20rpx;
  float: right;
}

.cc-new {
  font-size: 30rpx;
  color: grey;
  margin-top: 100rpx;
  .cc-return {
    font-size: 24rpx;
    cursor: pointer;
    color: #00bcd4;
  }
}

.cc-textarea {
  display: inline-block;
  padding-bottom: 8rpx;
  padding: 20rpx;
  outline: none;
  width: 100%;
  resize: none;
  border-radius: 2rpx;
  border-color: #eee;
  border-width: 4rpx;
}

.cc-commit {
  text-align: center;
  cursor: pointer;
  font-size: 24rpx;
  border-radius: 10rpx;
  width: 76rpx;
  height: 32rpx;
  margin-right: 52rpx;
  margin-top: 60rpx;
  margin-bottom: 16rpx;
  color: #fff;
  float: right;
  padding: 10rpx;
  background-color: #00bcd4;
}

.height-26 {
  height: 52rpx;
}

.talk-title {
  font-size: 24rpx;
  color: #447a96;
  margin-top: 36rpx;
}

.reply-style {
  cursor: pointer;
  color: #00bcd4;
}

.reply-content {
  color: rgb(75, 75, 75);
  font-size: 26rpx;
}

.talk-margin {
  margin: 80rpx auto 40rpx;
}

.block1 {
  border-left: 6rpx solid #447a96;
  padding-left: 20rpx;
}

.member {
  padding-bottom: 40rpx;
  position: relative;
}

.button-position1 {
  position: absolute;
  top: 30rpx;
  left: 40rpx;
}

.button-position2 {
  position: absolute;
  top: 30rpx;
  right: 40rpx;
}

.member-h3{
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.member-p {
  text-align: center;
}

.member-key {
  text-align: center;
  font-size: 32rpx;
  margin-bottom: 30rpx;
  padding-left: 0;
  padding-right: 0;
}

.member-none {
  text-align: center;
  cursor: pointer;
  color: rgb(0, 188, 212);
}